import {MenuLevel} from './MenuLevel';
import {LevelLink} from './LevelLink';

export class MenuTree {
  private levels: MenuLevel[];

  constructor() {
    let testItems = [
      new MenuLevel(
        [
          new LevelLink('', '/', -1),
          new LevelLink('', null, 1),
          new LevelLink('', 'dresses', -1),
          // new LevelLink('Back Stage', 'backstage', -1),
          new LevelLink('', 'contacts', -1),
          new LevelLink(' ', 'rules', -1),
          new LevelLink('',null,4),
          new LevelLink('', 'equipment', -1),
          new LevelLink('', 'portfolio', -1),
          new LevelLink('', 'calendar', -1),
        ],
        -1, 0),
        new MenuLevel(
          [
            new LevelLink('   ', 'den-rozhdenija', -1),
            // new LevelLink(' ', 'master-klassy', -1),
          ],
          0, 4),

      new MenuLevel(
        [
          new LevelLink('Open Space', 'open-space', -1),
          new LevelLink('Fashion Space', 'fashion-space', -1),
          new LevelLink('Loft Space', 'loft-space', -1),
          new LevelLink('White Space', 'white-space', -1),
          new LevelLink('Hygge Space', 'hygge-space', -1),
          new LevelLink('Make Up & Dress room', 'make-up-dress-room', -1)

        ],
        0, 1),
      new MenuLevel(
        [
          new LevelLink('', '/dresses/woman', -1),
          new LevelLink('', '/dresses/kids', -1),
        ], 0, 2),
    ];
    this.setLevels(testItems);
  }

  getLevels = (): MenuLevel[] => {
    return this.levels;
  }

  setLevels = (levels: MenuLevel[]): void => {
    this.levels = levels;
  }

  addLevel = (level: MenuLevel): void => {
    this.levels.push(level);
  }


}
