import { Component, OnInit } from '@angular/core';
import {UiService} from '../../services/ui-service/ui.service';
import {ApiService} from '../../services/api-service/api.service';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-page-services-dr',
  templateUrl: './page-services-dr.component.html',
  // styleUrls: ['./page-services-dr.component.scss']
  styleUrls: ['../HallsCommon/halls.scss']
})
export class PageServicesDrComponent implements OnInit {

  public hall = [];
  constructor(
    private UIservice: UiService, 
    private apiService: ApiService, 
    private router: ActivatedRoute,
  ) {

    setTimeout(function(){        
      // $('.app-loading').css('display','flex');
      // $('.app-loading').delay(1000).fadeOut('slow'); 
      // $('.app-dummy .left-layer').addClass('active');
      $('main').animate({
        opacity: 1,
      }, 800);
  }, 0);
  }


  openModal = (event) => {
    this.UIservice.toggleCallbackModal(event);
  }

  shallPlayVideo = (): boolean => {
    if (screen.width < 576) {
      return false;
    }
    return true;
  }

  toggleHallInfo = () => {
    let hallInfo = document.getElementsByClassName('hall-info')[0];
    if (screen.width < 576) {
      hallInfo.classList.toggle('--open');
    } else {
      hallInfo.classList.toggle('--closed');
    }
  };


  toggleVideoPlayback = (event) => {
    let video = document.getElementsByTagName('video')[0];
    this.toggleHallInfo();
    this.UIservice.toggleSidebar(event);
    this.UIservice.closeRightMenu(event);
    if (video.muted) {
      video.muted = false;
      video.style.cursor = 'url("../../../assets/full-screen-exit.png"), auto';
    } else {
      video.muted = true;
        video.style.cursor = 'url("../../../assets/full-screen.png"), auto';
    }
    // if (video.paused) {
    //   video.play();
    // } else {
    //   video.pause();
    // }
  }

  ngAfterViewInit(): void {
    try {
      let video = document.getElementsByTagName('video')[0];
      video.muted = true;
    } catch (e) {
    }
  }

  ngOnInit() {
    this.UIservice.listenHallInfoToggle().subscribe(() => {
      this.toggleHallInfo();
    });
    // this.hall = (this.router.snapshot.data.halls['data'].filter(item => item['title'] === 'FASHION SPACE'))[0];
  }


}
