  goBack = (): void => {
    if (this.stepper.selectedIndex === 1) {
      let checkboxes = Array.from(document.getElementsByClassName('hall-checkbox'));
      for (let item of checkboxes) {
        (item as HTMLInputElement).checked = false;
      }
      // this.formedOrder.setSelectedDate(this.stepControlTwo.get('selectedDate').value);
      this.stepControlTwo.get('selectedDate').setValue(null);
      this.stepControlOne.get('selectedHall').setValue(null);
      this.resetForm();
      this.total = this.getExtras();
    }
    this.stepper.previous();
  }
  goForward = (): void => {
    this.stepper.next();
  }
  moveToStep = (index) => {
    this.stepper.selectedIndex = index;
  }

  getUnavaliableTimes = (event) => {
    let time = event['value'].format('DDMMYYYY');
      if(this.stepControlOne.get('selectedHall').value !== null){
        this.apiService.getTimesUnavaliable(time, this.stepControlOne.get('selectedHall').value['product_id']).subscribe(value => {
          this.timesUnavaliable = value;
        });
      } else {
        this.apiService.getTimesUnavaliable(time, this.cart.order.getSelectedHall()['product_id']).subscribe(value => {
          this.timesUnavaliable = value;
        });
      }
      
      this.resetForm();
      this.total = this.getTotal() + this.getExtras();
   
  }

  

  resetForm = (): void => {
    $('.time__item').prop('checked',false);
    this.checkedCheckboxes = [];
    this.times.forEach(item => {
      item['checked'] = false;
    });
    this.total = 0;
    this.errorDate = false;
    this.errorTime = false;
  }

  checkTimeStatus = (item):boolean => {
    for (let time of this.timesUnavaliable) {
      if (time['interval'] === item['index']) {
        return false;
      }
    }
    return true;
  }

  timesOnChange = (formControlName, item): void => {
    this.errorTime = false;
    let hall = this.cart.order.getSelectedHall();
    let status = hall === null;
 
    if(!status){
      this.stepControlOne.get('selectedHall').setValue(hall);
    }

    if (!this.checkTimeStatus(item)) {
      return;
    }

    if(item['checked'] === true){
      const index = this.checkedCheckboxes.indexOf(item);
      if (index !== -1) {
        this.checkedCheckboxes.splice(index, 1);
      }
      item['checked'] = false;
    } else {
      this.checkedCheckboxes.push(item);
      item['checked'] = true;
    }

    
    this.total = this.getTotal() + this.getExtras();
  }

  setHall = (formControlName,item): void => {
    if (item.checked == null) {
      this.stepControlOne.get(formControlName).setValue(item);
      this.cart.order.setSelectedHall(item);
    } else {
      if (item.checked) {
        this.stepControlOne.get(formControlName).setValue(null);
        this.cart.order.setSelectedHall(null);

      } else {
        this.stepControlOne.get(formControlName).setValue(item);
        this.cart.order.setSelectedHall(item);

      }
      item.checked = !item.checked;
    }
  }

  validateCheckboxGroup = (): void => {
    if (this.checkedCheckboxes.length !== 0 && this.stepControlTwo.get('selectedDate').value !== null) {
      this.stepControlTwo.get('selectedTime').setValue(this.checkedCheckboxes);
      this.newTotal = this.stepControlOne.get('selectedHall').value['price'] * this.checkedCheckboxes.length;
      this.errorTime = false;
      this.errorDate = false;
      this.stepper.next();
    }
    
    if(this.checkedCheckboxes.length === 0){
        this.errorTime = true;
    }

    if(this.stepControlTwo.get('selectedDate').value === null){
      this.errorDate = true;
    }
    return ;
  }

  hallsOnChange = (formControlName, item): void => {
    if (item.checked == null) {
      this.stepControlOne.get(formControlName).setValue(item);
      this.cart.order.setSelectedHall(item);
    } else {
      if (item.checked) {
        this.stepControlOne.get(formControlName).setValue(null);
        this.cart.order.setSelectedHall(null);

      } else {
        this.stepControlOne.get(formControlName).setValue(item);
        this.cart.order.setSelectedHall(item);

      }
      item.checked = !item.checked;
    }
    this.stepper.next();
  }
 
  extrasOnChange = (formControlName, item): void => {
    if (item.checked == null) {
      this.stepControlThree.get(formControlName).setValue(item);
    } else {
      if (item.checked) {
        this.stepControlThree.get(formControlName).setValue(null);
      } else {
        this.stepControlThree.get(formControlName).setValue(item);
      }
      item.checked = !item.checked;
    }
    this.total = this.getTotal() + this.getExtras();
  }

  personsChange = (formControlName, index): void => {
    let item = null;
    for (let option of this.extras.numberOfPersons) {
      if (option['index'] == index) {
         item = option;
      }
    }
    this.extrasOnChange(formControlName,item);
  }

  changeDressingRoom = (formControlName, index): void => {
    let item = null;
    for (let option of this.extras.dressingRoom) {
      if (option['index'] == index) {
         item = option;
      }
    }
    this.extrasOnChange(formControlName,item);
  }


  getTotal = (): number => {
     return this.stepControlOne.get('selectedHall').value['price'] * this.checkedCheckboxes.length;
  }
    getExtras = (): number => {
      let summ = 0;
      let lightSource = this.stepControlThree.get('lightSource').value;
      let dressingRoom = this.stepControlThree.get('dressingRoom').value;
      let numberOfPersons = this.stepControlThree.get('numberOfPersons').value;
      let bathFilling = this.stepControlThree.get('bathFilling').value;
      if (lightSource) {
        summ += lightSource['price'];
      }
      if (dressingRoom) {
        summ += dressingRoom['price'];
      }
      if (numberOfPersons) {
        summ += numberOfPersons['price'] * this.checkedCheckboxes.length;
      }
      if (bathFilling) {
        summ += bathFilling['price'];
      }
       return summ;
   
    }

  recalculateDressesCost = (selectedDresses): void => {
    let sum = 0;
    for (let item of selectedDresses) {
      sum += item['price'];
    }
    this.dressesCost = sum - this.dressesCostOld;
    this.dressesCostOld = sum;
  }

  formOrder = (): void => {
    
    this.cart.order.setSelectedHall(this.stepControlOne.get('selectedHall').value);
    
    if(this.stepControlThree.get('numberOfPersons').value === null){
      this.stepControlThree.get('numberOfPersons').setValue(this.extras.numberOfPersons[0]);
    } 

    if(this.stepControlThree.get('dressingRoom').value === null){
      this.stepControlThree.get('dressingRoom').setValue(this.extras.dressingRoom[0]);
    } 
    
    this.goForward();
    //todo implement Builder
    this.formedOrder = new Order();
    this.formedOrder.setSelectedHall(this.cart.order.getSelectedHall());
    this.formedOrder.setSelectedDate(this.stepControlTwo.get('selectedDate').value);
    this.formedOrder.setSelectedTime(this.stepControlTwo.get('selectedTime').value);
    this.formedOrder.setLightSource(this.stepControlThree.get('lightSource').value);
    this.formedOrder.setExtraDressingRoom(this.stepControlThree.get('dressingRoom').value);
    this.formedOrder.setNumberOfPersons(this.stepControlThree.get('numberOfPersons').value);
    this.formedOrder.setBathFilling(this.stepControlThree.get('bathFilling').value);
    this.formedOrder.setDress(this.selectedDresses);
    this.cart.order = this.formedOrder;
  }

