import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'app-hall-info',
  templateUrl: './hall-info.component.html',
  styleUrls: ['./hall-info.component.scss']
})
export class HallInfoComponent implements OnInit {

  constructor() { }

  openHallInfo =  () => {
    let hallInfo = document.getElementsByClassName('hall-info')[0];
    let hallInfoOpener = document.getElementsByClassName('hall-info-opener')[0];
    let main = document.getElementsByTagName('main')[0];
    hallInfo.classList.add('--open');
    hallInfoOpener.classList.add('--hidden');
    document.getElementsByClassName('floating-price')[0].classList.add();
  };

  closeHallInfo = () => {
    let hallInfo = document.getElementsByClassName('hall-info')[0];
    let hallInfoOpener = document.getElementsByClassName('hall-info-opener')[0];
    let main = document.getElementsByTagName('main')[0];
    hallInfo.classList.remove('--open');
    hallInfoOpener.classList.remove('--hidden');
  };

  ngOnInit() {
  }

}
